<script>
import Loader from '@/components/Loader'

const SM_MESSAGE_OPEN_URL = 'openUrl'
const SM_MESSAGE_CHECK_IFRAME_SRC = 'checkIframeSrc'
const DARK_THEME_BACKGROUND_COLOR = '#121212'

export default {
  name: 'NewTabApp',

  components: { Loader },

  data () {
    return {
      isSpinnerVisible: false,
      isWaitingForPage: true,
    }
  },

  beforeCreate () {
    chrome.storage.local.get(['isDarkTheme', 'backgroundImage', 'backgroundColor'], (result) => {
      const backgroundContainer = document.createElement('div')

      backgroundContainer.style.position = 'fixed'
      backgroundContainer.style.zIndex = 0
      backgroundContainer.style.top = 0
      backgroundContainer.style.right = '16px'
      backgroundContainer.style.bottom = 0
      backgroundContainer.style.left = 0

      if (result.isDarkTheme) {
        document.body.classList.remove('body_no-header')
        backgroundContainer.style.backgroundImage = 'none'
        backgroundContainer.style.backgroundColor = DARK_THEME_BACKGROUND_COLOR
      } else if (result.backgroundImage) {
        document.body.classList.add('body_no-header')
        backgroundContainer.style.backgroundImage = `url("${result.backgroundImage}")`
        backgroundContainer.style.backgroundRepeat = 'no-repeat'
        backgroundContainer.style.backgroundPosition = 'center'
        backgroundContainer.style.backgroundSize = 'cover'
        backgroundContainer.style.backgroundColor = 'transparent'
      } else if (result.backgroundColor) {
        document.body.classList.remove('body_no-header')
        backgroundContainer.style.backgroundImage = 'none'
        backgroundContainer.style.backgroundColor = result.backgroundColor
      }

      document.body.prepend(backgroundContainer)
    })
  },

  mounted () {
    this.showSpinner()

    chrome.runtime.sendMessage({ message: SM_MESSAGE_OPEN_URL })
      .then((response) => {
        if (this.$refs.iframe) {
          this.$refs.iframe.src = response.url
          this.isWaitingForPage = false

          chrome.runtime.onMessage.addListener((request) => {
            if (request && request.message && request.message === SM_MESSAGE_CHECK_IFRAME_SRC && response.url === request.homepageUrl) {
              this.$refs.iframe.src = request.url
              return false
            }
          })
        }
      })
      .catch((e) => { })
  },

  methods: {
    showSpinner() {
      if (!this.$refs.loader) return

      this.isSpinnerVisible = true
    },
  },
}
</script>

<template>
  <div
    class="new-tab-screen"
    :class="{ 'new-tab-screen_prefilled': isWaitingForPage }"
  >
    <Loader
      class="new-tab-screen__loader"
      v-show="isSpinnerVisible"
      ref="loader"
      blocking
      hide-scrollbar
    />
    <iframe
      class="new-tab-screen__iframe"
      ref="iframe"
    />
  </div>
</template>

<style lang="scss">
html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  display: flex;
  align-items: stretch;

  &:not(.body_no-header)::before {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: 1;
    height: 46px;
    content: '';
    background-color: rgba($color-pure-black, 0.3);
  }

  &::after {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    width: 16px;
    content: '';
    background-color: $color-white;
  }
}

.new-tab-screen {
  width: 100%;
  height: 100%;

  &__iframe {
    position: relative;
    z-index: 2;
    width: 100%;
    height: 100%;
    border: 0;
    margin: 0;
    padding: 0;

    ::-webkit-scrollbar {
      background: $color-white;
    }
  }
}
</style>
